<template>
  <div :class="[isDesktop ? 'container' : 'container-fluid']" class="mb-2 mt-md-5 mt-sm-0">
    <div class="categories">
      <div class="row justify-content-around">
        <input type="checkbox" id="all" @click="setFilter" />

        <div class="col-auto m-1">
          <input type="checkbox" id="publication" @click="setFilter" />
          <label for="publication" class="category-filter category publication px-3">
            {{ filters.publication ? "&#10005;" : "" }} publication</label
          >
        </div>
        <div class="col-auto m-1">
          <input type="checkbox" id="moving" @click="setFilter" />
          <label for="moving" class="category-filter category moving px-3">
            {{ filters.moving ? "&#10005;" : "" }} moving-image</label
          >
        </div>
        <div class="col-auto m-1">
          <input type="checkbox" id="collage" @click="setFilter" />

          <label for="collage" class="category-filter category collage px-3">
            {{ filters.collage ? "&#10005;" : "" }} collage</label
          >
        </div>

         <div class="col-auto m-1">
          <input type="checkbox" id="artwork" @click="setFilter" />
          <label for="artwork" class="category-filter category artwork px-3">
            {{ filters.artwork ? "&#10005;" : "" }} artwork</label
          >
        </div>

        <div class="col-auto m-1">
          <input type="checkbox" id="editorial" @click="setFilter" />

          <label for="editorial" class="category-filter category editorial px-2">
            {{ filters.editorial ? "&#10005;" : "" }} editorial
            illustration</label
          >
        </div>

       
      </div>
    </div>
<div v-for="(post, index) in filteredProjects"
      :key="post.id"
      :id="post.id"
      :post="index">
    <router-link
      :to="'/projects/' + post.uid"
      tag="div"
      
      ref="post"
      :post="index"
      class="row justify-content-around"
      @mouseenter="addQuickFade"
    >
   
      <div class="col-12 col-lg-6 mb-3 mb-md-0 pb-md-0 order-lg-last">
        <div class="row">
          <div class="col-12">
            <SkeletonBox v-if="textLoaded" :min-width="20" :max-width="40" />
            <h6 v-if="!textLoaded" class="m-0 title-text turnorange">
              {{ post.data.title[0].text }}
            </h6>
          </div>

          <div class="col-12 my-1 mt-3 yeartext">
            <SkeletonBox v-if="textLoaded" :min-width="8" :max-width="15" />
            <p v-if="!textLoaded" class="m-0 turnorange">{{ post.data.year }}</p>
          </div>
          <div class="col-12">
            <SkeletonBox v-if="textLoaded" :min-width="10" :max-width="20" />
            <p v-if="!textLoaded" class="yeartext m-0 turnorange">
              {{ post.data.medium[0].text }}
            </p>
          </div>

          <div class="col-12 mt-3">
            <div v-if="textLoaded">
              <SkeletonBox :min-width="99" :max-width="100" />
              <SkeletonBox :min-width="99" :max-width="100" />
            </div>
            <p v-if="!textLoaded" v-snip="1" class="m-0 turnorange">
              {{ post.data.text[0].text }}
            </p>
          </div>
          <!-- <div class="col-12 mt-3">
              <div class="row justify-content-start">
              <div class="col-auto pb-2" v-for="category in post.data.category" :key="category"><div class="category px-3 turnorange" :class="categoryTag(category.category_option)">{{category.category_option}}</div></div>
           </div>
          </div> -->
        </div>
      </div>
       <div class="col-12 col-lg-6 mb-4 mb-md-0 order-lg-first position-relative">
        
        
          
        <img
          :width="post.data.image.dimensions.width"
          :height="post.data.image.dimensions.height"
          class="img-fluid lazyload project-image p-0"
          :data-src="post.data.image.url"
          data-expand="-20"
          alt=""
          :style="imageRatio(post.data.image)"
          
        />
         <div
            class="
            container-fluid
             project-icon
              d-flex
              justify-content-center
              align-items-center
            "
          >
          
            <img
              :width="post.data.image.dimensions.width"
              :height="post.data.image.dimensions.height"
              class="img-fluid lazyload icon-image"
              :data-src="post.data.icon.url"
              data-expand="0"
              alt=""
             
            />
            </div>
      </div>
    </router-link>
    <div class="row mb-md-5 pb-5"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectList",

  data() {
    return {
        isDesktop: true,
      hovering: null,
      filters: {
        all: true,
        publication: false,
        movingImage: false,
        collage: false,
        editorial: false,
        artwork: false,
      },
      posts: [],
      textLoaded: true,
      linkResolver: this.$prismic.linkResolver,
      hover: -1,
      isLoaded: false,
      currentHover: 100,
    };
  },
  methods: {
    getPosts() {
        this.windowSize();
      //Query to get blog posts
      this.$prismic.client
        .query(this.$prismic.Predicates.at("document.type", "post"), {
          orderings: "[my.post.year desc, my.post.month desc]",
        })
        .then((response) => {
          this.posts = response.results;
            this.textLoaded = false;
            
        });
    },
    postClass(index) {
      if (this.centerClass.includes(index)) return "justify-content-sm-center";
      else if (this.leftClass.includes(index))
        return "justify-content-sm-center";
      else if (this.rightClass.includes(index))
        return "justify-content-sm-center";
    },
    landscapePortrait(width, height) {
      if (width > height) return "col-sm-5";
      else return "col-sm-5";
    },
    minusOffsetTop(index) {
      if (index > 0) return "";
      else return "";
    },
    hoveringOver(el) {
      this.hovering = el;
    },
    addQuickFade(e) {
      e.target.classList.add("project-wrapper");
    },
    imageRatio() {
      // let height = e.dimensions.height;
      // let width = e.dimensions.width;
      // let ratio = width / height;
      // let maxHeight = (window.innerHeight / 100) * 50;
      // return {
      //   "max-height": maxHeight + "px",
      //   "max-width": maxHeight * ratio + "px",
      //   "min-width": maxHeight * ratio + "px",
      // };
    },
    setFilter(event) {
      const inputId = event.target.id;
      const isActive = event.target.checked;
      const updatedFilters = {
        ...this.filters,
        [inputId]: isActive,
      };

      // select only one
      let updatedFiltersArray = [];

      for (let item in updatedFilters) {
        updatedFiltersArray.push({
          item,
          value: updatedFilters[item],
        });
      }
      updatedFiltersArray.forEach((item) => {
        if (inputId === item.item) {
          item.value === !item.value;
        }
        if (item.item === "all") {
          item.value = false;
        }
        if (updatedFiltersArray.every((v) => v.value === false)) {
          if (item.item === "all") {
            item.value = true;
          }
        }
      });

      let object = {};
      for (let i = 0; i < updatedFiltersArray.length; i++) {
        object[updatedFiltersArray[i].item] = updatedFiltersArray[i].value;
      }
      this.filters = object;
    },
    // categoryTag(el) {
    //     if(el === 'publication') {
    //         return 'publication' 
    //     }
    //     if(el === 'editorial illustration') {
    //         return 'editorial' 
    //     }
    //      if(el === 'artwork') {
    //         return 'artwork' 
    //     }
    //      if(el === 'moving-image') {
    //         return 'moving' 
    //     }
    //      if(el === 'collage') {
    //         return 'collage' 
    //     }
    // },
     windowSize() {
      let screenSize = window.innerWidth;
      if (screenSize > 768) {
        this.isDesktop = true;
      } else {
        this.isDesktop = false;
      }
    }
  },
  computed: {
    filteredProjects() {
      const posts = this.posts;

      return posts.filter((post) => {
        if (
          this.filters.publication &&
          post.data.category.some((e) => e.category_option === "publication")
        ) {
          return true;
        }
        if (
          this.filters.moving &&
          post.data.category.some((e) => e.category_option === "moving-image")
        ) {
          return true;
        }
        if (
          this.filters.collage &&
          post.data.category.some((e) => e.category_option === "collage")
        ) {
          return true;
        }
        if (
          this.filters.editorial &&
          post.data.category.some(
            (e) => e.category_option === "editorial illustration"
          )
        ) {
          return true;
        }
        if (
          this.filters.artwork &&
          post.data.category.some((e) => e.category_option === "artwork")
        ) {
          return true;
        }
        if (this.filters.all) {
          return true;
        }
        return false;
      });
    },
  },

  created() {
    this.getPosts();
  },
};
</script>

